import React, { Component } from 'react';
import './ComponentCSS.css';


class Comp08_LifeCycle extends Component {
    render() {
        return (
            <div className="ComponentCSS">
                <h1>Life Cycle Component</h1>
                <LifeComp />
            </div>
        );
    }
}

class LifeComp extends Component {
    state = {
        number: 0
    }

    constructor(props) {
        //   
        super(props);
        console.log('constructor');
    }
    componentWillMount() {
        //     
        //     v16.3 deprecated 
        console.log('componentWillMount');
    }
    componentDidMount() {
        //   : D3, Masonry, etc
        //    : Ajax, GraphQL etc
        // DOM  :  , ,  
        console.log('componentDidMount');
    }
    static getDeriveStateFromProps() {
        // v16.3 
        // props   state      
        //  props     state    .
        /*
        if (nextProps.value !== prevState.value){
            return {value : nextProps.value}
        }
        return null // null       
        */
        console.log('getDeriveStateFromProps');
    }
    shouldComponentUpdate(nextProps, nextState) {
        //      ,    ,
        //    .   render()   
        //  'true' 
        // false  render()  
        console.log('shouldComponentUpdate');
        // number 5  
        if (nextState.number % 3 === 0) {
            return false;
        }
        else{
            return true;
        }
    }
    componentWillUpdate(nextProps, nextState) {
        // shouldComponentUpdate true    
        //     ,    
        //    render() 
        // v16.3  depecate,   getSnapshotBeforeUpdate 
        console.log('componentWillUpdate');

    }
    getSnapshotBeforeUpdate(prevProps, prevState) {
        /*
        *************** getSnapshotBeforeUpdate  
        1. render()
        2. getSnapshotBeforeUpdate()
        3.  DOM 
        4. componentDidUpdate() //   return   3  

        
        */
        console.log('getSnapshotBeforeUpdate');
    }
    handleIncrease = () => {
        const { number } = this.state;
        this.setState({
            number: number + 1
        });
    }

    handleDecrease = () => {
        this.setState(
            ({ number }) => ({
                number: number - 1
            })
        );
    }

    render() {
        console.log('render');
        return (
            <div>
                <h1>LifeCycle </h1>
                <h2>3    </h2>
                <div>: {this.state.number}</div>
                <button onClick={this.handleIncrease}>+</button>
                <button onClick={this.handleDecrease}>-</button>
            </div>
        )
    }
}

export default Comp08_LifeCycle;


